import React, { Component } from 'react';

class SideNavbar extends Component {

    moveToDiv(ref){
		var elementpane = document.querySelector("#"+ref);
		elementpane.scrollIntoView({behavior:"smooth",block:"start",inline:"nearest"})
    }	
    
    

    render() {
        return (
            <div className="section hide-on-small-only" style={{'margin-top':'180px','position':'fixed'}}>
                <div className="col m2 s2 l1"></div>
                <div className="col m8 s8 l10">
                <ul class="collection">
                    <a class="collection-item waves-effect waves-light btn" style={{'height':'50px'}} onClick={e=>this.moveToDiv('profileDiv')}>Profile</a>
                    <a class="collection-item waves-effect waves-light btn" style={{'height':'50px'}} onClick={e=>this.moveToDiv('educationDiv')}>Education</a>
                    <a class="collection-item waves-effect waves-light btn" style={{'height':'50px'}} onClick={e=>this.moveToDiv('experiencesDiv')}>Experience</a>
                    <a class="collection-item waves-effect waves-light btn" style={{'height':'50px'}} onClick={e=>this.moveToDiv('skillsDiv')}>Skills</a>
                    <a class="collection-item waves-effect waves-light btn" style={{'height':'50px'}} onClick={e=>this.moveToDiv('languagesDiv')}>Languages</a>
                    <a class="collection-item waves-effect waves-light btn" style={{'height':'50px'}} onClick={e=>this.moveToDiv('contactDiv')}>Contact</a>
                </ul>
                </div>
                <div className="col m2 s2 l1"></div>
            </div>
        );
    }
}

export default SideNavbar;